// .front-info
.front-info {
	background-color: rgba(#eee, .5);
}

// Freespace
.freespace {
	figcaption {
		color: #fff;
		background-color: rgba(#222, .6);
	}
}

// Blog
.front-blog {
  &__article {
    .thumb {
      background-color: #f6f6f6;
    }
    .datetime {
      color: #888;
    }
  }

  &__article-link {
    &:before,
    &:after {
      content: "";
      position: absolute;
      z-index: 1;
      display: block;
      width: 0;
      height: 0;
      border-width: 1px;
      border-style: solid;
      transition: 350ms ease;
    }
    &:before {
      top: 0;
      left: 0;
      border-right-width: 0;
      border-bottom-width: 0;
      border-top-color: transparent;
      border-left-color: transparent;
    }
    &:after {
      right: 0;
      bottom: 0;
      border-left-width: 0;
      border-top-width: 0;
      border-right-color: transparent;
      border-bottom-color: transparent;
    }

    &:hover {
      &:before,
      &:after {
        width: 30%;
        height: 30%;
      }
      &:before {
        border-top-color: $default-color;
        border-left-color: $default-color;
        transform: translate(-3px, -3px);
      }
      &:after {
        border-right-color: $default-color;
        border-bottom-color: $default-color;
        transform: translate(3px, 3px);
      }
    }
  }
}
// Catalog
.front-catalog-wrap {
	.overlay {
		background-color: rgba(#fff, .6);
		&:before {
			content: "";
			position: absolute;
			right: 0;
			bottom: 0;
			display: block;
			width: 0;
			height: 0;
			border: 15px solid transparent;
			border-right-color: $default-color;
			border-bottom-color: $default-color;
		}
	}
	.title {
		color: #222;
	}
	.stylist {
		color: #222;
	}
	.swiper-slide {
		background: #f0f0f0;
	}

	$border-width: 2px;
	@media (min-width: $screen-tab + 1) {
		.swiper-button-prev,
		.swiper-button-next {
			&:before {
				border-top: $border-width solid $swiper-border-color;
			}
		}
		.swiper-button-prev {
			&:before {
				border-left: $border-width solid $swiper-border-color;
			}
		}
		.swiper-button-next {
			&:before {
				border-right: $border-width solid $swiper-border-color;
			}
		}
	}
}


// Front Social
.instagram {
	& > li {
		box-shadow: 0 2px 3px 1px rgba(#222, .3);
		time {
			color: #555;
		}
	}
}
.social-heading {
	border-bottom: 1px solid #ddd;
}
