.en {
	font-family: $font-google;
}
.c-title {
	.en {
		padding-left: 40px;
		padding-right: 40px;
		font-size: 40px;
		&:before, &:after {
			content: "";
			position: absolute;
			display: block;
			width: 0;
			height: 0;
			border: 8px solid transparent;
		}
		&:before {
			top: 0;
			left: 0;
			border-top-color: $default-color;
			border-left-color: $default-color;
		}
		&:after {
			right: 0;
			bottom: 0;
			border-right-color: $default-color;
			border-bottom-color: $default-color;
		}
	}
	.jp {
		
	}
}
.c-title-sm {
	.en {
		font-size: 28px;
	}
}
.c-title-page {
	.en {
		position: relative;
		padding-left: 24px;
		padding-right: 24px;
		font-size: 40px;
		&:before, &:after {
			content: "";
			position: absolute;
			display: block;
			width: 0;
			height: 0;
			border: 8px solid transparent;
		}
		&:before {
			top: 0;
			left: 0;
			border-top-color: $default-color;
			border-left-color: $default-color;
		}
		&:after {
			right: 0;
			bottom: -14px;
			border-right-color: $default-color;
			border-bottom-color: $default-color;
		}
	}
	.jp {
		font-size: 14px;
		color: #888;
	}
	@media (min-width: $screen-tab) {
		.en {
			padding-left: 40px;
			padding-right: 40px;
		}
	}
}
