.post-archive {
  border-color: #ccc;
	.not-thumb {
		background: #f5f5f5;
	}
	.cat {
		color: #888;
		background-color: #fcfcfc;
		border: 1px solid #ddd;
	}
	.datetime {
		font-family: $font-google;
		color: #888;
	}
	.content {
		color: #222;
	}
	@media (max-width: $screen-tab - 1) {
		background-color: #fff;
		.post-link {
			&:hover {
				background-color: #fcfcfc;
			}
		}
		.title {
		}
	}
}

.post-single {
	.post-header {
		// border-bottom: 1px solid #ddd;
	}

	.datetime {
		font-family: $font-google;
		font-size: 16px;
		color: #666;
	}
	.cat {
		a {
			font-size: 12px;
			background-color: #f5f5f5;
			border: 1px solid #ddd;
		}
	}
	@media (min-width: $screen-tab) {
		.datetime {
			font-size: 20px;
		}
	}
}
.social-count {
	.fa-facebook, .fa-twitter {
		color: #aaa;
	}
	.count {
		color: #666;
	}
}

.single-pager {
	border-top: 1px solid #ddd;
	a {
		background-color: #fff;
		border: 1px solid #ddd;
	}
}
