$drawer-width: 220px;
// Drawer Navigation
@media (max-width: $screen-tab) {
  #drawerOverlay {
    background-color: rgba(#222, .7);
  }
}

#drawer {
  background-color: #fff;
}


// ドロワーメニュー用のボタン
.drawer {
  &-btn-close {
    background-color: #222;
    border-left-color: #000;
    &:hover {
      background-color: #922;
    }
  }
}
// ドロワーメニュー
.drawer {
  &-navi {
    background-color: #eaeaea;
    &:before {
      background-color: #fff;
    }
    .logo-area {
      color: #fff;
      @include sgradient(
        $colors: (
          (lighten($navi-color-mode, 10%), 0%),
          ($navi-color-mode, 100%)
        )
      );
      border-bottom: 1px solid #999;
    }
  }
  &-inner {
    background-color: #eaeaea;
  }
}
.drawer-gnavi {
  & > li {
    &.current-menu-item {
      background-color: #e0e0e0;
      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 0;
        height: 0;
        border: 8px solid transparent;
        border-top-color: $navi-color-mode;
        border-left-color: $navi-color-mode;
      }
    }
    & > a {
      color: $navi-color-jp;
      border-bottom: 1px solid #ccc;

      > .ja {
        color: $navi-color-jp;
      }
      > .en {
        font-family: $font-google;
        color: $navi-color-en;
      }
      &.hover {
        color: #e6e6e6;
        background-color: #222;
        > .ja {
          color: #e6e6e6;
        }
        > .en {
          color: #e6e6e6;
        }
      }
    }
  }
} // ul.drawer-gnavi

.drawer-subnavi {
  & > li {
    border-bottom: 1px solid #ccc;
    & > a {
      color: #fff;
      background-color: #444;
      &:hover {
        background-color: #222;
      }
    }
  }
}

// グローバルナビゲーション
.gnavi {
  & > li {
    a {
      color: $navi-color-mode;
    }
    &:last-child {
      & > a {
        &:after {
          border-right: 1px solid $navi-color-line;
        }
      }
    }
    &.current-menu-item, &:hover {
      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 0;
        height: 0;
        border: 8px solid transparent;
        border-top-color: $navi-color-mode;
        border-left-color: $navi-color-mode;
      }
    }
    & > a {
      color: $navi-color-jp;

      > .ja {
        color: $navi-color-jp;
      }

      > .en {
        font-family: $font-google;
        color: $navi-color-en;
      }
      &:hover {
        background-color: transparent;
      }
      &:after {
        border-left: 1px solid $navi-color-line;
      }
    }
  }
}
