// Staff Archives ---*/
.staff-loop {
	.manage {
		color: #888;
	}
}
@media (max-width: $screen-tab - 1) {
	.staff-loop {
		.staff-name {
			background-color: rgba(#fff, .6);
		}
	}
}
@media (min-width: $screen-tab) {
	.staff-loop {
		.staff-name {
			background: rgba(#fff, .6);
		}
	}
}

// Staff Single ---*/
.staff-header {
	.manage {
		& > span {
			border: 1px solid #ddd;
		}
	}
}
.staff-table {
	th,td {
		border-bottom: 1px solid #ddd;
	}
	th {
		color: #888;
	}
}
.staff-comment {
	background-color: #f5f5f5;
}
@media (min-width: $screen-tab) {
	.staff-header {
		.title {
			border-bottom: 1px solid #ddd;
		}
	}
}

// Other Staff ---*/
.other-staff {
	background-color: #eee;
	.title {
		color: #666;
		.en {
			font-family: $font-google;
		}
	}
}
.other-staff-list {
	.item {
		color: #888;
	}
	.manage {
		background-color: #fcfcfc;
		border: 1px solid #ddd;
	}
	.staff-list-next,
	.staff-list-prev {
		color: $default-text-color;
		background: $default-color;
	}
}
