.stylist-category {
	background-color: #f0f0f0;
	border: 1px solid #ddd;
	.title {
		color: $text-color-header;
		border-bottom: 1px solid #ddd;
	}
}

// Catalog Search
.catalog-search-wrapper {
	background-color: #fff;
}
.catalog_search {
	.cr {
		label {
			background-color: #fff;
		}
		input {
			&:checked {
				& + label {
					color: $btn-default-color;
					background-color: $btn-default-bg-color;
				}
			}
		}

	}
}

// Catalog LoopContent
.catalog-loop {
	.item {
		background-color: #fff;
		box-shadow: 0 1px 3px 0px rgba(#333, .2);
		transition: all 100ms ease-in;
		&:hover {
			opacity: .8;
		}
		.not-thumb {
			background-color: rgba(#ccc, .3);
		}
		.border-catalog {
			border-top: 1px solid #888;
		}
		.meta {
			.manage {
				span {
					border: 1px solid #ccc;
				}
			}
			.staff {
				// font-size: 16px;
			}
		}
	}
}


// Single
.catalog-picture {
	&-body {
		.pic-wrap {
			background-color: #fafafa;
		}
	}

	&-list {
		&-item {
			&.active {
				border-color: $default-color;
			}
		}
	}

	.not-picture {
		color: rgba(#fff, .3);
		background-color: rgba($default-color, .8);
	}
}

// Catalog Content
.catalog-header {
	.title {
		border-bottom: 1px solid #ddd;
	}
	.comment {
		background: #f5f5f5;
		&:after {
			border-left: 14px solid #f5f5f5;
		}
	}
	.staff-pic {
		.thumb {
			background-color: rgba(#ccc, .3);
		}
	}
}

.catalog-table {
	th,td {
		border-bottom: 1px solid #ddd;
	}
	th {
		color: #888;
	}
}
.catalog-tag {
	& > li {
		a {
			background-color: #fcfcfc;
			border: 1px solid #ddd;
		}
	}
}
.catalog-price {
	.price {
		font-family: $font-google;
		color: #888;
		.numeric {
			color: #454545;
		}
	}
}

.catalog-footer {
	.other-title {
		font-family: $font-google;
		color: #666;
		border-bottom: 1px solid #ddd;
	}
}
.other-catalog {
	.swiper-slide {
		a {
			background-color: rgba(#ccc, .3);
		}
		.not-thumb {
			background-color: rgba(#ccc, .3);
		}
	}
}


